/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
.wp-block-create-block-profilecard {
	border-radius: 50px;
}
.profile-card {
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
	text-align: center;
	position: relative;
	overflow: hidden;
	background-color: rgb(173, 208, 35);
}

.profile-pic {
	overflow: hidden;
	position: absolute;
	top: 50px;
	left: 50%;
	transform: translateX(-50%);
}
.profile-pic img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.pro-name {
	margin-top: 80px;
	font-weight: bold;
}
.role {
	margin: 5px 0;
}
.description {
	padding: 0 20px;
}
.stats {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 20px;
	padding: 20px 0;
}
.stat {
	text-align: center;
}
.stat .pro-num {
	margin: 0;
}
.stat .pro-foll {
	margin: 5px 0;
}
.btn {
	background-color: black;
	color: rgb(255, 255, 255);
	border: none;
	padding: 10px 20px;
	letter-spacing: 2px;
	font-size: 15px;
	border-radius: 10px;
	cursor: pointer;
	margin-bottom: 20px;
	display: inline-block;
}
